  @layer utilities {
   /*** ESTILOS BOTÓN GROW SKEW ***/
.ov-btn-grow-skew {
  background: rgb(55 65 81); /* color de fondo */
  color: #fff; /* color de fuente (azul) */
  /*border: 2px solid #e9e9e9; /* tamaño y color de borde (azul) */
  position: relative;
  z-index: 1;
  overflow: hidden;
  display: inline-block;
}

.ov-btn-grow-skew:hover {
  color: #fff; /* color de fuente hover */
}


.ov-btn-grow-skew::after {
  content: "";
  /*background: #0091ff; /* color de fondo hover (azul) */
  background: linear-gradient(to top right, #0091ff, #c368d7);
  position: absolute;
  z-index: -1;
  padding: 16px 20px;
  display: block;
  left: -20%;
  right: -20%;
  top: 0;
  bottom: 0;
  transform: skewX(-45deg) scale(0, 1);
  transition: all 0.3s ease;
  
  
}

.ov-btn-grow-skew:hover::after {
  transition: all 0.3s ease-out;
  transform: skewX(-45deg) scale(1, 1);
}


